.main_section_wrapper {
	background: white;
}

.mainpage_block {
	position: relative;
	padding-top: 30px;
	padding-bottom: 10px;
}

.mainpage_block.style1 {
	background: white;
}

.mainpage_block.style2 {
	background: #FFE4B5;
}

.mainpage_block .description {
	width: 80%;
	margin: 10px auto;
	font-size: 18px;
	text-align: center;
}

.mainpage_block .block_header {
	font-size: 24px;
	font-weight: normal;
	text-align: center;
	margin-top: 5px;
	margin-bottom: 5px;
}

.mainpage_block .next.button {
	margin: 10px 60px 25px 60px;
	width: 200px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	background: papayawhip;
	border: 1px solid peru;
	border-radius: 10px;
	box-shadow: 1px 1px 4px grey;
	cursor: pointer;
	color: black;
}

.mainpage_block.style1 .next.button {
	float: right;
}

.mainpage_block.style2 .next.button {
	float: left;
}

.mainpage_block .next.button:hover {
	background: white;
	box-shadow: 2px 2px 3px grey;
}

.mainpage_block .arrow {
	position: absolute;
	width: 60px;
	height: 60px;
	bottom: -30px;
	left: 0;
	right: 0;
	margin: auto;
	z-index: 1500;
}

@media screen and (max-width: 768px) {
    .mainpage_block.style1 .next.button, 
    .mainpage_block.style2 .next.button {
		float: none;
		margin: 10px auto 35px auto;
    }
}