.product_page .title {
	margin: 10px auto;
	font-size: 24px;
	text-align: center;
}

.params_panel {
	position: relative;
	width: 640px;
	margin: 10px auto;
}

.params_panel .prodId {
	color: grey;
	text-align: right;
}

.params_panel .price {
	font-size: 24px;
	font-weight: bold;
	color: green;
	text-align: right;
}

.product_page .favorits_button {
	position: absolute;
	bottom: 5px;
	left: 5px;
	width: 50px;
	height: 50px;
	user-select: none;
	cursor: pointer;
}

.product_page .favorits_button svg {
	width: 100%;
	height: 100%;
}

.product_page .favorits_button.add {
	color: grey;
	opacity: 0.5;
}

.product_page .favorits_button.remove {
	color: red;
}

.product_page p {
	padding: 10px;
	text-indent: 25px;
	text-align: justify;
}

.back_button {
	width: 160px;
	height: 40px;
	margin: 20px auto 40px auto;
	background: navajowhite;
	border: 1px solid tan;
	border-radius: 10px;
	box-shadow: 1px 1px 4px grey;
	line-height: 40px;
	text-align: center;
	font-size: 16px;
	cursor: pointer;
}

.back_button:hover {
	background: white;
	box-shadow: 2px 2px 3px grey;
}

@media screen and (max-width: 700px) {
	.params_panel {
		width: 90%;

	}
}