.post_block {
	box-shadow: 1px 1px 5px grey;
	border-radius: 10px;
	margin: 10px;
	padding-bottom: 5px;
	max-height: 400px;
}

.post_block_body {
	max-height: 360px;
	overflow: hidden;
}

.post_block.style1 {
	background: #FFF8DC;
}

.post_block.style2 {
	background: white;
}

.post_block .header_time_block {
	text-align: center;
}

.header_time_block .post_header {
	display: inline-block;
	margin: 0 10px;
	font-size: 16px;
}

.header_time_block .post_time {
	display: inline-block;
	margin: 0 10px;
}

.post_block .post_img_wrap {
	width: 340px;
	height: 340px;
	margin: auto;
}

.post_block.style1 .post_img_wrap {
	float: right;
}

.post_block.style2 .post_img_wrap {
	float: left;
}

.post_block img {
	margin: 10px;
	border-radius: 10px;
	box-shadow: 2px 2px 4px grey;
}

.post_block .post_text_wrap {
	height: 340px;
	overflow: hidden;
}

.post_block .post_text_wrap p {
	margin: 20px;
	text-align: justify;
	text-indent: 20px;
}

.post_block .to_post.button {
	margin: 10px auto;
	width: 160px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	border: 1px solid peru;
	border-radius: 10px;
	box-shadow: 1px 1px 4px grey;
	cursor: pointer;
	color: black;
}

.post_block.style1 .to_post.button {
	background: #FFE4B5;
}

.post_block.style2 .to_post.button {
	background: papayawhip;
}

.post_block .to_post.button:hover {
	background: white;
	box-shadow: 2px 2px 3px grey;
}

@media screen and (max-height: 1080px) {
    .post_block {
		max-height: 480px;
	}

	.post_block_body {
		max-height: 430px;
	}
}

@media screen and (max-height: 800px) {
    .post_block {
		max-height: 480px;
	}

	.post_block_body {
		max-height: 400px;
	}
}

@media screen and (max-width: 768px) {
	.post_block {
		max-height: none;
		border-radius: 0;
		margin: 10px 0;
	}

	.post_block_body {
		max-height: none;
	}
}

@media screen and (max-width: 660px) {
	.post_block.style1 .post_img_wrap {
		float: none;
	}

	.post_block.style2 .post_img_wrap {
		float: none;
	}
}