.slider_section {
	width: 100%;
	padding: 0;
	text-align: center;
}

.slider_body {
	width: 990px;
	height: 360px;
	margin: 0 auto;
}

.slider_item {
	height: 350px;
}

.slider_item .slide_header {
	font-size: 16px;
	margin: 0;
	padding: 0;
}

.slider_item .slide_header:hover {
	color: darkorange;
	text-decoration: underline;
}

.slider_image {
	position: relative;
	width: 300px;
	height: 300px;
	margin: 10px auto;
	border-radius: 10px;
	box-shadow: 2px 2px 6px grey;
	overflow: hidden;
}

.slider_image img {
	width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.intro {
	width: 50%;
	margin: 10px auto;
	text-align: center;
}

.style1 .slick-prev:before,.style1 .slick-next:before {
	font-size: 20px;
	color: #FFE4B5;
	text-shadow: 1px 1px 2px grey;
}

.style2 .slick-prev:before,.style2 .slick-next:before {
	font-size: 20px;
	color: white;
	text-shadow: 1px 1px 2px grey;
}

@media screen and (max-width: 1140px) {
    .slider_body {
		width: 660px;
	}
}

@media screen and (max-width: 740px) {
    .slider_body {
		width: 340px;
	}
}