.contacts_page h2 {
	text-align: center;
}

.contacts_page >.links {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 340px;
}

.contacts_page .links .link_item {
	display: flex;
	align-items: center;
	color: #8B0000;
}

.contacts_page .links .link_item:hover {
	color: chocolate;
}

.contacts_page .links .icon_wrap {
	position: relative;
	width: 50px;
	height: 50px;
	margin: 10px;
}

.contacts_page .links .icon_wrap svg {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 25px;
	height: 25px;
	margin: auto;
}

.contacts_page .link_text {
	font-size: 22px;
}

@media screen and (max-width: 680px) {
	.contacts_page .link_text {
		font-size: 18px;
	}	
}

@media screen and (max-width: 480px) {
	.contacts_page .link_text {
		font-size: 14px;
	}	
}

@media screen and (max-width: 400px) {
	.contacts_page .link_text {
		font-size: 12px;
	}	
}