.main_menu {
	height: 60px;
	min-height: 60px;
	color: #8B4513;
	border-top: 3px double #D2691E;
	border-bottom: 3px double #D2691E;
}

.main_menu_wrapper {
	position: absolute;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: linear-gradient(0deg, #F4A460, #DEB887 50%);
	z-index: 2000;
}

.main_menu ul {
	width: 80%;
	max-width: 1000px;
	height: 60px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	margin: 0 0 0 20px;
	padding: 0;
	list-style: none;
}

.main_menu li .button_wrap {
	position: relative;
	width: 130px;
	height: 42px;
}

.main_menu li .button {
	position: absolute;
	top: 0;
	left: 0;
	height: 40px;
	width: 120px;
	background: papayawhip;
	border: 2px solid peru;
	border-radius: 10px;
	box-shadow: 1px 1px 4px grey;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-shadow: 1px 1px 3px #F4A460;
	transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
}

.main_menu li .button:hover {
	height: 42px;
	width: 126px;
	line-height: 42px;
	background: white;
	box-shadow: 2px 2px 4px grey;
	border-radius: 11px;
	color: sandybrown;
}

.main_menu li .button.active {
	position: absolute;
	top: 0;
	left: -10px;
	height: 52px;
	width: 140px;
	background: white;
	border: 2px solid peru;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	border-bottom: 0;
	box-shadow: none;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-shadow: 1px 1px 3px #F4A460;
	transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
}

.main_menu li .button.active:hover {
	position: absolute;
	top: 0;
	left: -10px;
	height: 52px;
	width: 140px;
	background: white;
	border: 2px solid peru;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	border-bottom: 0;
	box-shadow: none;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-shadow: 1px 1px 3px #F4A460;
	transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
}

.main_menu li .button.active.gallery {
	background: #FFF8DC;
}

.main_menu li .button.active.gallery:hover {
	background: #FFF8DC;
}

.main_menu a {
	text-decoration: none;
	color: saddlebrown;
}

.main_menu a:visited {
	color: saddlebrown;
}

.main_menu .lang_select {
	margin: 5px 10px;
	width: 180px;
	min-width: 180px;
	text-align: right;
}

.main_menu .lang_select select {
	margin-left: 10px;
	width: 50px;
	height: 28px;
	border: 2px solid #D2691E;
	box-shadow: 2px 2px 2px grey;
	border-radius: 4px;
}

@media screen and (max-width: 1140px) {
	.main_menu ul {
		max-width: 800px;
	}

    .main_menu li .button_wrap {
		width: 110px;
	}

	.main_menu li .button {
		width: 100px;
		font-size: 14px;
	}

	.main_menu li .button:hover {
		width: 106px;
	}

	.main_menu li .button.active {
		left: -5px;
		width: 110px;
	}

	.main_menu li .button.active:hover {
		left: -5px;
		width: 110px;
	}
}

@media screen and (max-width: 940px) {
	.main_menu ul {
		max-width: 600px;
	}

    .main_menu li .button_wrap {
		width: 90px;
	}

	.main_menu li .button {
		width: 80px;
		height: 36px;
		line-height: 36px;
		font-size: 12px;
	}

	.main_menu li .button:hover {
		width: 86px;
		height: 38px;
		line-height: 38px;
	}

	.main_menu li .button.active {
		left: -2px;
		width: 94px;
	}

	.main_menu li .button.active:hover {
		left: -2px;
		width: 94px;
	}
}

@media screen and (max-width: 840px) {
	.main_menu {
		height: 0;
		min-height: 0;
		border-top: none;
		border-bottom: 3px double #D2691E;
	}

	.main_menu_wrapper {
		display: none;
	}
}