.side_mainmenu {
	position: fixed;
	top: 20px;
	right: 20px;
	width: 60px;
	height: 42px;
	background: grey;
	border-radius: 8px;
	opacity: 0.5;
	transition: width 0.3s ease, 
				height 0.1s ease,
				opacity 0.3s ease,
				background 0.3s ease;
	z-index: 5000;
	overflow: hidden;
}

.side_mainmenu.topview {
	height: 0;
}

.side_mainmenu .wrapper {
	position: relative;
	width: 100%;
	height: 100%;
}

.side_mainmenu .mini {
	position: absolute;
	top: 0;
	right: 0;
	width: 60px;
	height: 42px;
	opacity: 1;
	transition: opacity 0.3s ease;
}

.side_mainmenu .mini div {
	width: 46px;
	height: 6px;
	margin: 6px auto;
	border-radius: 2px;
	background: black;
}

.side_mainmenu .list {
	margin: 0;
	padding: 40px 30px;
	list-style: none;
	background: linear-gradient(rgba(128, 128, 128, 0.5), rgba(128, 128, 128, 0.2));
	border: 2px solid #D2691E;
	border-radius: 10px;
	opacity: 0;
	transition: opacity 0.3s ease;
}

.side_mainmenu .list li div {
	margin-bottom: 10px;
	height: 40px;
	width: 180px;
	background: navajowhite;
	border: 1px solid #D2691E;
	box-shadow: 1px 1px 4px grey;
	border-radius: 10px;
	line-height: 40px;
	text-align: center;
	text-shadow: 3px 2px 6px #F4A460;
}

.side_mainmenu .list li div:hover {
	background: white;
	box-shadow: 2px 2px 3px grey;
}

.side_mainmenu .list a {
	text-decoration: none;
	color: #8B0000;
}

.side_mainmenu .list a:visited {
	color: #8B0000;
}

.side_mainmenu .lang_select {
	text-align: right;
	margin-top: 40px;
}

.side_mainmenu .lang_select select {
	margin-top: 0;
	margin-left: 10px;
	width: 50px;
	height: 24px;
	border: 1px solid #D2691E;
	box-shadow: 2px 2px 2px grey;
	border-radius: 4px;
}

.side_mainmenu:hover {
	width: 240px;
	height: 500px;
	opacity: 1;
	background: none;
}

.side_mainmenu:hover .mini {
	opacity: 0;
}

.side_mainmenu:hover .list {
	opacity: 1;
}

.up_button {
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 42px;
	height: 42px;
	background: grey;
	border-radius: 8px;
	opacity: 0.5;
	z-index: 5000;
	transition:	height 0.1s ease;
	overflow: hidden;
	cursor: pointer;
}

.up_button.topview {
	height: 0;
}

.up_button svg {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 34px;
	height: 34px;
}

@media screen and (max-width: 840px) {
	.side_mainmenu.topview {
		height: 42px;
	}	

	.side_mainmenu:hover {
		width: 240px;
		height: 500px;
		opacity: 1;
		background: none;
	}
}