.arrow {
	cursor: pointer;
}

.arrow_wrap {
	position: relative;
	width: 60px;
	height: 60px;
}

.arrow_down_wrap {
	position: absolute;
	width: 100%;
	height: 100%;
	border-bottom-right-radius: 20%;
	transform: rotateZ(45deg);
}

.arrow_down_wrap.style1 {
	background: white;
}

.arrow_down_wrap.style2 {
	background: #FFE4B5;
}

.arrow_down {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 67%;
	height: 67%;
	margin: auto;
	background: #DEB887;
	border-bottom-right-radius: 20%;
}

.arrow_top_wrap {
	position: absolute;
	width: 100%;
	height: 50%;
}

.arrow_top_wrap.style1 {
	background: white;
}

.arrow_top_wrap.style2 {
	background: #FFE4B5;
}

.arrow_top {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	width: 50%;
	height: 30%;
	margin: auto;
	background: #DEB887;
}

.arrow_top div {
	position: absolute;
	width: 50px;
	left: -20px;
	right: -20px;
	bottom: 10px;
	margin: auto;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
}