.paging_section .pages {
	display: flex;
	justify-content: center;
	width: 340px;
	margin: 5px auto;
}

.paging_section .page {
	position: relative;
	width: 26px;
	height: 26px;
	line-height: 26px;
	margin: 5px 10px;
	text-align: center;
	background: navajowhite;
	border: 1px solid tan;
	border-radius: 50%;
	box-shadow: 1px 1px 4px grey;
	cursor: pointer;
}

.paging_section .page:hover {
	background: white;
	box-shadow: 2px 2px 3px grey;
}

.paging_section .page.current {
	background: white;
	border: 1px solid #8B4513;
}

.paging_section .page svg {
	width: 18px;
	height: 18px;
	margin: 5px;
	color: #8B4513;
}