.gallery_item {
	position: relative;
	width: 340px;
	height: 400px;
	margin: 10px;
	border: 1px solid grey;
	border-radius: 10px;
	background: white;
	box-shadow: 1px 1px 4px grey;
}

.gallery_item .title {
	text-align: center;
	font-size: 18px;
	margin: 2px 0;
}

.gallery_item img {
	border-radius: 10px;
	margin: 0 10px;
	box-shadow: 0px 0px 2px grey;
}

.gallery_item .prodId {
	margin-right: 10px;
	font-size: 14px;
	color: grey;
	text-align: right;
}

.gallery_item .price {
	margin-right: 10px;
	text-align: right;
	font-size: 20px;
	color: green;
}

.gallery_item .to_page {
	position: absolute;
	width: 100px;
	height: 20px;
	left: 0;
	right: 0;
	bottom: 8px;
	margin: auto;
	background: #8BEE92;
	border: 1px solid tan;
	border-radius: 6px;
	box-shadow: 1px 1px 4px grey;
	line-height: 20px;
	text-align: center;
}

.gallery_item .to_page:hover {
	background: #D4F9D7;
	box-shadow: 2px 2px 3px grey;
}

.gallery_item .favorit_button {
	position: absolute;
	width: 30px;
	height: 30px;
	bottom: 6px;
	left: 10px;
	user-select: none;
	cursor: pointer;
}

.gallery_item .favorit_button.remove {
	color: red;
}

.gallery_item .favorit_button.add {
	color: gray;
	opacity: 0.5;
}


.gallery_item .favorit_button svg {
	width: 100%;
	height: 100%;
}

@media screen and (max-width: 400px) {
	.gallery_item {
		margin: 10px 0;
	}
}