.header {
	position: relative;
	display: flex;
	align-items: center;
	height: 240px;
	margin: 0;
	padding: 20px;
	background: url("https://images-na.ssl-images-amazon.com/images/I/416Cm11FIUL.jpg");
	/*background: linear-gradient(0deg, #F4A460, #DEB887 50%);*/
	overflow: hidden;
}

.header .logo_wrap {
	display: flex;
	z-index: 2;
}

.header .logo_wrap h2 {
	padding: 0;
	margin: 0;
	margin-left: 10px;
	font-size: 65px;
	line-height: 80px;
	color: #8B4513;
	font-style: italic;
	text-shadow: 4px 4px 8px wheat;
	z-index: 3000;
}

.header .logo_wrap .img_wrap {
	position: relative;
	width: 160px;
	height: 160px;
	border: 4px solid #8B4513;
	background: radial-gradient(wheat, tan, peru);
	border-radius: 50%;
	box-shadow: 2px 2px 4px wheat, 2px 2px 4px wheat inset;
}

.header .img_wrap img {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 128px;
	height: 128px;
}

.header .back_foto {
	width: 960px;
	height: 200px;
	right: 40px;
	z-index: 1;
	overflow: hidden;
	border-radius: 960px/200px;
	box-shadow: 4px 4px 8px sienna;
}

.header .back_foto img {
	width: 100%;
	height: 100%;
	object-position: center;
	object-fit: cover;
}

@media screen and (max-width: 1366px) {
    .header .back_foto {
    	width: 600px;
		height: 200px;
		border-radius: 600px/200px;
    }
}

@media screen and (max-width: 1280px) {
    .header .back_foto {
    	width: 480px;
		height: 200px;
		border-radius: 480px/200px;
    }
}

@media screen and (max-width: 1140px) {
    .header .back_foto {
    	width: 360px;
		height: 200px;
		border-radius: 360px/200px;
    }

    .header .logo_wrap h2 {
		font-size: 55px;
		line-height: 80px;
	}
}

@media screen and (max-width: 960px) {
    .header .back_foto {
    	display: none;
    }

    .header .logo_wrap h2 {
		font-size: 65px;
		line-height: 80px;
	}
}

@media screen and (max-width: 640px) {
    .header .logo_wrap h2 {
		font-size: 55px;
		line-height: 80px;
	}
}

@media screen and (max-width: 600px) {
	.header {
		display: block;
		height: 340px;
		margin: 0 auto;
		background: url("https://images-na.ssl-images-amazon.com/images/I/416Cm11FIUL.jpg");
	}

    .header .logo_wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		z-index: 2;
	}

	.header .logo_wrap .img_wrap {
		position: relative;
		width: 160px;
		height: 160px;
		margin: 10px;
		border: 4px solid #8B4513;
		background: radial-gradient(wheat, tan, peru);
		border-radius: 50%;
		box-shadow: 2px 2px 4px wheat, 2px 2px 4px wheat inset;
	}

	.header .logo_wrap h2 {
		text-align: center;
		font-size: 60px;
		line-height: 65px;
		color: #8B4513;
		font-style: italic;
		text-shadow: 4px 4px 8px wheat;
		z-index: 3000;
	}
}

@media screen and (max-width: 480px) {
    .header .logo_wrap h2 {
		font-size: 46px;
		line-height: 56px;
	}
}