.header {
	position: relative;
	display: flex;
	align-items: center;
	height: 240px;
	margin: 0;
	padding: 20px;
	background: url("https://images-na.ssl-images-amazon.com/images/I/416Cm11FIUL.jpg");
	/*background: linear-gradient(0deg, #F4A460, #DEB887 50%);*/
	overflow: hidden;
}

.header .logo_wrap {
	display: flex;
	z-index: 2;
}

.header .logo_wrap h2 {
	padding: 0;
	margin: 0;
	margin-left: 10px;
	font-size: 65px;
	line-height: 80px;
	color: #8B4513;
	font-style: italic;
	text-shadow: 4px 4px 8px wheat;
	z-index: 3000;
}

.header .logo_wrap .img_wrap {
	position: relative;
	width: 160px;
	height: 160px;
	border: 4px solid #8B4513;
	background: -webkit-radial-gradient(wheat, tan, peru);
	background: radial-gradient(wheat, tan, peru);
	border-radius: 50%;
	box-shadow: 2px 2px 4px wheat, 2px 2px 4px wheat inset;
}

.header .img_wrap img {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 128px;
	height: 128px;
}

.header .back_foto {
	width: 960px;
	height: 200px;
	right: 40px;
	z-index: 1;
	overflow: hidden;
	border-radius: 960px/200px;
	box-shadow: 4px 4px 8px sienna;
}

.header .back_foto img {
	width: 100%;
	height: 100%;
	object-position: center;
	object-fit: cover;
}

@media screen and (max-width: 1366px) {
    .header .back_foto {
    	width: 600px;
		height: 200px;
		border-radius: 600px/200px;
    }
}

@media screen and (max-width: 1280px) {
    .header .back_foto {
    	width: 480px;
		height: 200px;
		border-radius: 480px/200px;
    }
}

@media screen and (max-width: 1140px) {
    .header .back_foto {
    	width: 360px;
		height: 200px;
		border-radius: 360px/200px;
    }

    .header .logo_wrap h2 {
		font-size: 55px;
		line-height: 80px;
	}
}

@media screen and (max-width: 960px) {
    .header .back_foto {
    	display: none;
    }

    .header .logo_wrap h2 {
		font-size: 65px;
		line-height: 80px;
	}
}

@media screen and (max-width: 640px) {
    .header .logo_wrap h2 {
		font-size: 55px;
		line-height: 80px;
	}
}

@media screen and (max-width: 600px) {
	.header {
		display: block;
		height: 340px;
		margin: 0 auto;
		background: url("https://images-na.ssl-images-amazon.com/images/I/416Cm11FIUL.jpg");
	}

    .header .logo_wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		z-index: 2;
	}

	.header .logo_wrap .img_wrap {
		position: relative;
		width: 160px;
		height: 160px;
		margin: 10px;
		border: 4px solid #8B4513;
		background: -webkit-radial-gradient(wheat, tan, peru);
		background: radial-gradient(wheat, tan, peru);
		border-radius: 50%;
		box-shadow: 2px 2px 4px wheat, 2px 2px 4px wheat inset;
	}

	.header .logo_wrap h2 {
		text-align: center;
		font-size: 60px;
		line-height: 65px;
		color: #8B4513;
		font-style: italic;
		text-shadow: 4px 4px 8px wheat;
		z-index: 3000;
	}
}

@media screen and (max-width: 480px) {
    .header .logo_wrap h2 {
		font-size: 46px;
		line-height: 56px;
	}
}
.main_menu {
	height: 60px;
	min-height: 60px;
	color: #8B4513;
	border-top: 3px double #D2691E;
	border-bottom: 3px double #D2691E;
}

.main_menu_wrapper {
	position: absolute;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: -webkit-linear-gradient(bottom, #F4A460, #DEB887 50%);
	background: linear-gradient(0deg, #F4A460, #DEB887 50%);
	z-index: 2000;
}

.main_menu ul {
	width: 80%;
	max-width: 1000px;
	height: 60px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	margin: 0 0 0 20px;
	padding: 0;
	list-style: none;
}

.main_menu li .button_wrap {
	position: relative;
	width: 130px;
	height: 42px;
}

.main_menu li .button {
	position: absolute;
	top: 0;
	left: 0;
	height: 40px;
	width: 120px;
	background: papayawhip;
	border: 2px solid peru;
	border-radius: 10px;
	box-shadow: 1px 1px 4px grey;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-shadow: 1px 1px 3px #F4A460;
	-webkit-transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
	transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
}

.main_menu li .button:hover {
	height: 42px;
	width: 126px;
	line-height: 42px;
	background: white;
	box-shadow: 2px 2px 4px grey;
	border-radius: 11px;
	color: sandybrown;
}

.main_menu li .button.active {
	position: absolute;
	top: 0;
	left: -10px;
	height: 52px;
	width: 140px;
	background: white;
	border: 2px solid peru;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	border-bottom: 0;
	box-shadow: none;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-shadow: 1px 1px 3px #F4A460;
	-webkit-transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
	transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
}

.main_menu li .button.active:hover {
	position: absolute;
	top: 0;
	left: -10px;
	height: 52px;
	width: 140px;
	background: white;
	border: 2px solid peru;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	border-bottom: 0;
	box-shadow: none;
	font-size: 16px;
	line-height: 40px;
	text-align: center;
	text-shadow: 1px 1px 3px #F4A460;
	-webkit-transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
	transition: height 0.1s ease,
				width 0.1s ease,
				line-height 0.1s ease,
				background 0.1s ease;
}

.main_menu li .button.active.gallery {
	background: #FFF8DC;
}

.main_menu li .button.active.gallery:hover {
	background: #FFF8DC;
}

.main_menu a {
	text-decoration: none;
	color: saddlebrown;
}

.main_menu a:visited {
	color: saddlebrown;
}

.main_menu .lang_select {
	margin: 5px 10px;
	width: 180px;
	min-width: 180px;
	text-align: right;
}

.main_menu .lang_select select {
	margin-left: 10px;
	width: 50px;
	height: 28px;
	border: 2px solid #D2691E;
	box-shadow: 2px 2px 2px grey;
	border-radius: 4px;
}

@media screen and (max-width: 1140px) {
	.main_menu ul {
		max-width: 800px;
	}

    .main_menu li .button_wrap {
		width: 110px;
	}

	.main_menu li .button {
		width: 100px;
		font-size: 14px;
	}

	.main_menu li .button:hover {
		width: 106px;
	}

	.main_menu li .button.active {
		left: -5px;
		width: 110px;
	}

	.main_menu li .button.active:hover {
		left: -5px;
		width: 110px;
	}
}

@media screen and (max-width: 940px) {
	.main_menu ul {
		max-width: 600px;
	}

    .main_menu li .button_wrap {
		width: 90px;
	}

	.main_menu li .button {
		width: 80px;
		height: 36px;
		line-height: 36px;
		font-size: 12px;
	}

	.main_menu li .button:hover {
		width: 86px;
		height: 38px;
		line-height: 38px;
	}

	.main_menu li .button.active {
		left: -2px;
		width: 94px;
	}

	.main_menu li .button.active:hover {
		left: -2px;
		width: 94px;
	}
}

@media screen and (max-width: 840px) {
	.main_menu {
		height: 0;
		min-height: 0;
		border-top: none;
		border-bottom: 3px double #D2691E;
	}

	.main_menu_wrapper {
		display: none;
	}
}
.side_mainmenu {
	position: fixed;
	top: 20px;
	right: 20px;
	width: 60px;
	height: 42px;
	background: grey;
	border-radius: 8px;
	opacity: 0.5;
	-webkit-transition: width 0.3s ease, 
				height 0.1s ease,
				opacity 0.3s ease,
				background 0.3s ease;
	transition: width 0.3s ease, 
				height 0.1s ease,
				opacity 0.3s ease,
				background 0.3s ease;
	z-index: 5000;
	overflow: hidden;
}

.side_mainmenu.topview {
	height: 0;
}

.side_mainmenu .wrapper {
	position: relative;
	width: 100%;
	height: 100%;
}

.side_mainmenu .mini {
	position: absolute;
	top: 0;
	right: 0;
	width: 60px;
	height: 42px;
	opacity: 1;
	-webkit-transition: opacity 0.3s ease;
	transition: opacity 0.3s ease;
}

.side_mainmenu .mini div {
	width: 46px;
	height: 6px;
	margin: 6px auto;
	border-radius: 2px;
	background: black;
}

.side_mainmenu .list {
	margin: 0;
	padding: 40px 30px;
	list-style: none;
	background: -webkit-linear-gradient(rgba(128, 128, 128, 0.5), rgba(128, 128, 128, 0.2));
	background: linear-gradient(rgba(128, 128, 128, 0.5), rgba(128, 128, 128, 0.2));
	border: 2px solid #D2691E;
	border-radius: 10px;
	opacity: 0;
	-webkit-transition: opacity 0.3s ease;
	transition: opacity 0.3s ease;
}

.side_mainmenu .list li div {
	margin-bottom: 10px;
	height: 40px;
	width: 180px;
	background: navajowhite;
	border: 1px solid #D2691E;
	box-shadow: 1px 1px 4px grey;
	border-radius: 10px;
	line-height: 40px;
	text-align: center;
	text-shadow: 3px 2px 6px #F4A460;
}

.side_mainmenu .list li div:hover {
	background: white;
	box-shadow: 2px 2px 3px grey;
}

.side_mainmenu .list a {
	text-decoration: none;
	color: #8B0000;
}

.side_mainmenu .list a:visited {
	color: #8B0000;
}

.side_mainmenu .lang_select {
	text-align: right;
	margin-top: 40px;
}

.side_mainmenu .lang_select select {
	margin-top: 0;
	margin-left: 10px;
	width: 50px;
	height: 24px;
	border: 1px solid #D2691E;
	box-shadow: 2px 2px 2px grey;
	border-radius: 4px;
}

.side_mainmenu:hover {
	width: 240px;
	height: 500px;
	opacity: 1;
	background: none;
}

.side_mainmenu:hover .mini {
	opacity: 0;
}

.side_mainmenu:hover .list {
	opacity: 1;
}

.up_button {
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 42px;
	height: 42px;
	background: grey;
	border-radius: 8px;
	opacity: 0.5;
	z-index: 5000;
	-webkit-transition:	height 0.1s ease;
	transition:	height 0.1s ease;
	overflow: hidden;
	cursor: pointer;
}

.up_button.topview {
	height: 0;
}

.up_button svg {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 34px;
	height: 34px;
}

@media screen and (max-width: 840px) {
	.side_mainmenu.topview {
		height: 42px;
	}	

	.side_mainmenu:hover {
		width: 240px;
		height: 500px;
		opacity: 1;
		background: none;
	}
}
.slider_section {
	width: 100%;
	padding: 0;
	text-align: center;
}

.slider_body {
	width: 990px;
	height: 360px;
	margin: 0 auto;
}

.slider_item {
	height: 350px;
}

.slider_item .slide_header {
	font-size: 16px;
	margin: 0;
	padding: 0;
}

.slider_item .slide_header:hover {
	color: darkorange;
	text-decoration: underline;
}

.slider_image {
	position: relative;
	width: 300px;
	height: 300px;
	margin: 10px auto;
	border-radius: 10px;
	box-shadow: 2px 2px 6px grey;
	overflow: hidden;
}

.slider_image img {
	width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.intro {
	width: 50%;
	margin: 10px auto;
	text-align: center;
}

.style1 .slick-prev:before,.style1 .slick-next:before {
	font-size: 20px;
	color: #FFE4B5;
	text-shadow: 1px 1px 2px grey;
}

.style2 .slick-prev:before,.style2 .slick-next:before {
	font-size: 20px;
	color: white;
	text-shadow: 1px 1px 2px grey;
}

@media screen and (max-width: 1140px) {
    .slider_body {
		width: 660px;
	}
}

@media screen and (max-width: 740px) {
    .slider_body {
		width: 340px;
	}
}
.arrow {
	cursor: pointer;
}

.arrow_wrap {
	position: relative;
	width: 60px;
	height: 60px;
}

.arrow_down_wrap {
	position: absolute;
	width: 100%;
	height: 100%;
	border-bottom-right-radius: 20%;
	-webkit-transform: rotateZ(45deg);
	        transform: rotateZ(45deg);
}

.arrow_down_wrap.style1 {
	background: white;
}

.arrow_down_wrap.style2 {
	background: #FFE4B5;
}

.arrow_down {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 67%;
	height: 67%;
	margin: auto;
	background: #DEB887;
	border-bottom-right-radius: 20%;
}

.arrow_top_wrap {
	position: absolute;
	width: 100%;
	height: 50%;
}

.arrow_top_wrap.style1 {
	background: white;
}

.arrow_top_wrap.style2 {
	background: #FFE4B5;
}

.arrow_top {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	width: 50%;
	height: 30%;
	margin: auto;
	background: #DEB887;
}

.arrow_top div {
	position: absolute;
	width: 50px;
	left: -20px;
	right: -20px;
	bottom: 10px;
	margin: auto;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
}

.post_block {
	box-shadow: 1px 1px 5px grey;
	border-radius: 10px;
	margin: 10px;
	padding-bottom: 5px;
