@media screen and (max-height: 1080px) {
    .post_block {
		max-height: 480px;
	}

	.post_block_body {
		max-height: 430px;
	}
}

@media screen and (max-height: 800px) {
    .post_block {
		max-height: 480px;
	}

	.post_block_body {
		max-height: 400px;
	}
}

@media screen and (max-width: 768px) {
	.post_block {
		max-height: none;
		border-radius: 0;
		margin: 10px 0;
	}

	.post_block_body {
		max-height: none;
	}
}

@media screen and (max-width: 660px) {
	.post_block.style1 .post_img_wrap {
		float: none;
	}

	.post_block.style2 .post_img_wrap {
		float: none;
	}
}



