.filter_menu {
	position: relative;
	z-index: 10000;
	transition: min-width 0.5s ease, width 0.5s ease;
}

.filter_menu .wrapper {
	position: fixed;
	top: 0;
	transition: width 0.5s ease;
}

.filter_menu.show {
	width: 260px;
	min-width: 260px;
}

.filter_menu.show .wrapper {
	width: 260px;
}

.filter_menu.hide {
	width: 0px;
	min-width: 0px;
}

.filter_menu.hide .wrapper {
	width: 0px;
}

.filter_menu .side_slider {
	height: 100%;
	transition: margin-left 0.5s ease;
}

.filter_menu.show .side_slider {
	margin-left: 0;
}

.filter_menu.hide .side_slider {
	margin-left: -260px;
}

.filter_menu .body {
	width: 250px;
	height: 100%;
	min-width: 250px;
	border: 2px solid chocolate;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
	overflow: hidden;
	box-shadow: 1px 1px 3px grey;
}

.filter_menu .search_filter {
	position: relative;
	height: 30%;
	border-bottom: 2px solid chocolate;
	background: burlywood;
}

.filter_menu .search_filter div {
	position: absolute;
	bottom: 10px;
	left: 0;
	right: 0;
	margin: auto;
}

.search_filter .search_radio {
	margin-left: 15px;
}

.search_filter p {
	margin: 0;
	padding: 10px;
}

.search_filter .search_string {
	width: 90%;
	margin: 0 10px;
}

.filter_menu .group_filter {
	height: 67%;
	background: burlywood;
	padding: 10px 10px;
	border-bottom-right-radius: 10px;
	overflow-y: scroll;
}

.group_filter .filter_radio {
	margin-bottom: 20px;
}

.group_filter .all_checkbox {
	margin-left: 20px;
}

.group_filter .checkbox {
	margin-left: 30px;
}

.filter_menu .active {
	background: wheat;
}

.filter_menu .showhide {
	position: absolute;
	top: 0;
	bottom: 0;
	right: -25px;
	width: 50px;
	min-width: 50px;
	height: 50px;
	margin: auto 0;
	border: 2px solid chocolate;
	border-radius: 50%;
	background: cornsilk;
	cursor: pointer;
	box-shadow: 1px 1px 2px grey;
	transition: right 0.5s ease;
}

.filter_menu.hide .showhide {
	right: -50px;
}

.showhide svg {
	width: 30px;
	height: 30px;
	margin: 10px;
	color: chocolate;
}

@media screen and (max-width: 800px) {
	.filter_menu {
		position: absolute;
		top: 0;
		left: 0;
	}
}