.footer {
	display: flex;
	justify-content: space-between;
	height: 160px;
	margin-top: 25px;
	border-top: 4px double chocolate;
	background: linear-gradient(0deg, #F4A460, #DEB887 50%);
}

.footer .footer_leftblock {
	display: flex;
}

.footer .footer_logo {
	display: flex;
	width: 340px;
}

.footer .footer_logo .img_wrap {
	position: relative;
	width: 100px;
	height: 100px;
	margin: 10px;
	border: 4px solid #8B4513;
	background: radial-gradient(wheat, tan, peru);
	border-radius: 50%;
	box-shadow: 2px 2px 4px wheat, 2px 2px 4px wheat inset;
}

.footer .footer_logo .img_wrap img {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 80px;
	height: 80px;
}

.footer .footer_logo .footer_logo_title {
	font-size: 20px;
	line-height: 24px;
	color: #8B4513;
}

.footer .footer_menu {
	width: 340px;
	padding-left: 25px;
}

.footer .footer_menu .list {
	list-style: none;
	margin: 0;
	padding: 10px 20px;
	border-left: 2px solid chocolate;
}

.footer .footer_menu .list li div {
	text-shadow: 1px 1px 2px #FFE4B5;
}


.footer .footer_menu .list li div:hover {
	color: darkorange;
	text-decoration: underline;
}

.footer .links {
	display: flex;
	justify-content: center;
	width: 340px;
}

.footer .links div {
	position: relative;
	width: 50px;
	height: 50px;
	margin: 10px;
	border: 1px solid chocolate;
	border-radius: 50%;
	box-shadow: 1px 1px 4px chocolate;
	background: #FFE4B5;
}

.footer .links div:hover {
	background: white;
	box-shadow: 2px 2px 3px chocolate;
}

.footer .links div svg {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 25px;
	height: 25px;
	margin: auto;
	color: #8B0000;
}

.footer .links div:hover svg {
	color: chocolate;
}

@media screen and (max-width: 1020px) {
	.footer {
		height: 130px;
	}

	.footer .footer_menu {
		display: none;
	}
}

@media screen and (max-width: 700px) {
	.footer .footer_logo {
		width: 140px;
	}

	.footer .footer_logo .footer_logo_title {
		display: none;
	}
}

@media screen and (max-width: 460px) {
	.footer .footer_logo {
		display: none;
	}

	.footer .links {
		width: 100%;
		justify-content: flex-start;
	}
}