.resolutionShow {
	position: fixed;
	color: red;
	z-index: 10000;
}

html, body, #root {
	width: 100%;
	height: 100%;
	min-width: 360px;
	margin: 0;
	padding: 0;
	font-family: Comic Sans MS;
	font-style: italic;
}

div {
	margin: 0;
	padding: 0;
}

.wrapper.center {
	margin: 0 auto;
}

.wrapper_main {
	position: relative;
	width: 100%;
	margin: 0 auto;
}

.main_column {
	position: relative;
	width: 80%;
	max-width: 1280px;
	margin: 0 auto;
}

.float_left {
	float: left;
}

.float_right {
	float: right;
}

.clear_fix {
	clear: both;
}

a {
	text-decoration: none;
	color: #8B0000;
}

a {
	color: #8B0000;
}

.load_screeen {
	position: fixed;
	top: 0;
	left: 0;
	background: #FFF8DC;
}

.spinner {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100px;
	height: 100px;
	margin: auto;
}

