.favorits_section {
	width: 1480px;
	max-width: 1480px;
	margin: auto;
}

.favorits_section .items {
	display: flex;
	flex-wrap: wrap;
}

@media screen and (max-width: 1380px) {
	.favorits_section {
		width: 1120px;
		max-width: 1120px;
	}
}

@media screen and (max-width: 1160px) {
	.favorits_section {
		width: 750px;
		max-width: 750px;
	}
}

@media screen and (max-width: 750px) {
	.favorits_section {
		width: 360px;
		max-width: 360px;
	}
}