.main_img {
	width: 640px;
	height: 640px;
	margin: 10px auto;
	border: 1px solid grey;
	border-radius: 20px;
	box-shadow: 1px 1px 4px grey;
	overflow: hidden;
}

.main_img img {
	width: 100%;
}

.previews {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.img_prev {
	width: 80px;
	height: 80px;
	margin: 6px;
	border: 1px solid darkorange;
	border-radius: 10px;
	box-shadow: 1px 1px 4px grey;
	overflow: hidden;
}

.img_prev.grey {
	filter: grayscale(100%);
}

.img_prev img {
	width: 100%;
	height: 100%;
}

@media screen and (max-width: 660px) {
	.main_img {
		width: 100%;
		height: auto;
		margin: 10px auto;
		border: none;
		border-radius: 0px;
		box-shadow: none;
		overflow: hidden;
	}

	.main_img img {
		box-shadow: 1px 1px 4px grey;
	}
}